import { Tab, Tabs, Swipe, SwipeItem,List , Toast} from "vant";
import XHeader from '../common/header.vue'
import { Category ,queryList } from "@/api/user";
export default {
  name: "Everyday",
  components: { XHeader,Tab, Tabs,  Swipe,SwipeItem, List ,Toast },
  data() {
    return{
      contEmpty:true,
      active: 0,
      Navlist: [
        {product_name:"推荐",id:0,level:1,parent_id:0,sort:0},        
      ],
      Alllist:[],
      loading: false,
      finished: false,
      pages:1,
      total_pages:1,
      categoryid:"",
      order_time:"",
      Kpage:"每日上新",
    };
  },
  
  created(){ 
        //Toast.loading({ mask: true,message: '加载中...'});
        let route = this.$storage.getSession("Everyday");
        this.Kpage =route.type
        for(var i = 0;i<this.Navlist.length;i++){
           if(route.key == this.Navlist[i].name){
                this.active = i
                return
           }
        }
        if(route.type == "每日上新"){
           this.order_time = 'desc'
        }
        this.Categorys()
        this.comete()
  },
  methods: {
    goBack(){this.$router.go(-1);},
    change() {
      this.Alllist = []
      this.pages = 1
      if (this.active == 0) { 
        this.categoryid = ""
      }else {
        this.categoryid = this.Navlist[this.active].id
      }
      this.comete()
    },
    onLoad() {
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          this.comete();
        }
        this.loading = false;
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
    async Categorys() {//请求数据接口 
      let res = await Category({
        
      });
      this.Categorylist = res.data
      //level:一级导航 
      //{2级 parent_id } <==>{1级 = ID}
      let headLiet =[]
    
      res.data.map(item =>{
         if(item.level == 1) headLiet.push(item)  
      })
      this.Navlist = this.Navlist.concat(headLiet)
    },
    async comete() {//请求数据接口 
        let bbb =[]
        let res = await queryList({
            specs:JSON.stringify(bbb),
            recommend:"",
            order_price:"", 
            order_time:"",
            category_id:this.categoryid,
            min_price:"", 
            max_price:"",
            min_weight:"",
            max_weight:"",  
            min_inner_diameter:"", 
            max_inner_diameter:"",
            page:this.pages
        });
         for(var i = 0; i< res.data.length ; i ++){
             for(var j=0;j<res.data[i].images.length ; j++){
                 res.data[i].images[j] = res.data[i].images[j].replace(/shop.jigesi.test/,'192.168.0.160')
             }
        }
        this.Alllist = this.Alllist.concat(res.data) 
        if( this.Alllist == ''){ this.contEmpty = false }
        this.total_pages = res.meta.pagination.total_pages
    },
    Details(id){
        let commodity = {}
        commodity.Pid =id
        this.$storage.setSession('commodity', commodity) 
        this.$router.push({path: "/Details"}); 
    }
  }
};
