import { Uploader } from 'vant';
import { categoryWithSpecById ,Category } from "@/api/user";
import { Addpro } from "@/api/shop";
import Cookie from "js-cookie";
import axios from "axios";
let config = {
        headers:{
            'Content-Type':'multipart/form-data',
            'Authorization':'Bearer '+ JSON.parse(localStorage.getItem('token'))//取出Cookie
        }
};
export default {
  name: "release",
  components:{
    Uploader
  },
  data() {    
    return{ 
        Subpage:false,
        Subpage1:false,
        Subfide:false,
        condition:[],
        Subname:"分类",
        Subname1:"xx",
        Sublist:[],
        children:[],
        children1:{Thlevel:[]},
        specslist:[],
        type:0,
        pages:0,
        keyname:{name:"请选择",id:""},
/////////////////////////////////////
        product_name:"", //标题 
        description:"", //描述
        images:[], // 图片
        credentials:[], //证书
        price:"",//价格
        weight:"",//重量
        hasGold:0, // XXXXX
        category_id:this.$route.query.ID,
        size:"",//尺寸
        size1:"",
        size2:"",
        specs:[], //属性
        
    };
  },
  created(){ 
        let  edit =  this.$storage.getSession("AdEdit");
         if(this.$route.query.pae == 1){
             this.product_name = edit.product_name
             this.description = edit.description
             this.images = edit.images
             this.price = edit.price
             this.weight = edit.weight
            this.size = edit.size[0]
            this.size1 = edit.size[1]
            this.size2 =edit.size[2]
           
         }
        console.log(edit)
        this.Categors()
        this.Categ()
  },
  methods: {
      okshu(){
        this.Subfide = false
      },
        relkey(index){
          this.type = index
        },
        cancel(){ //取消
          if(this.$route.query.pae == 1){
              this.$router.push({ path: "/Administration"});
          }else{
               this.$router.go(-2);
          }
        },
        colis(index,key){ //删除图片
            if(key == 0 ){
                this.images.splice(index, 1);
            }else{
                this.credentials.splice(index, 1);
            } 
        },
       child(){//种类
           this.Subpage = true
           document.documentElement.style.overflowY = 'hidden';
           this.pages = 0
           this.Subname ='分类' 
       },
       gory(item){ //点击种类
            console.log(item)
            if(item.Thlevel !=''){
                this.children1 = item
                this.Subpage1 = true;
                this.Subname1 = item.product_name
            }else{
                this.keyname ={
                    name:item.product_name,
                    id:item.id
                }
                this.Subpage = false;
                this.category_id = item.id;
                this.Categors()
                this.specs = []
                document.documentElement.style.overflowY = 'scroll'; 
            }  
       },
        gory1(item){ //点击种类1
                this.keyname ={
                    name: this.children1.product_name +' — '+ item.product_name,
                    id:item.id
                }
                this.Subpage1 = false;
                this.Subpage = false; 
                this.category_id = item.id;
                this.Categors()
                this.specs = [] 
                document.documentElement.style.overflowY = 'scroll'; 
        },
        Abute(item){
            if(this.specs.length < 1){
                this.specs.push({specId:item.specId,specValueId:item.id})
            }else{
                this.specslist.map(key => {
                   if(key.id === item.specId)     key.kmane = item.spec_value
                })
                this.Subpage = false;
                for(let i = 0;i<this.specs.length;i++){
                    if(this.specs[i].specId == item.specId){
                        this.specs[i] = {specId:item.specId,specValueId:item.id}
                        return
                    }
                }
                this.specs.push({specId:item.specId,specValueId:item.id})
            }
          
            
        },
       spesk(item,index){//属性
           this.Sublist = item.spec_values 
           this.Sublist.map(iktem=>{
               iktem.specId=item.id
           })
            this.Subname = item.spec_name 
           this.pages = 1
            this.Subpage = true
       },
       lowSub(){
            this.Subpage = false ;
           document.documentElement.style.overflowY = 'scroll'; 
       },
       lowSub1(item){
        this.Subpage1= false ;
         document.documentElement.style.overflowY = 'scroll';
       },
        async Categ(){//请求所有分类
            let res = await Category({});
            this.Categorylist = res.data;

            //level:一级导航
            //{2级 parent_id } <==>{1级 = ID}
            console.log(res)
            let headLiet = [];
            let c_id = this.$route.query.ID
            if(this.$route.query.pae == 1){
                let pae = this.$route.query.pae
                let  edit =  this.$storage.getSession("AdEdit").category_id; 
                let Kitem = res.data
                for(var j=0; j< Kitem.length;j++ ){
                    if(Kitem[j].id == edit){
                        this.keyname = {name:Kitem[j].product_name,id:Kitem[j].id}
                        if(Kitem[j].level != 1){
                             for(var i=0; i< Kitem.length;i++ ){
                                 console.log(1)
                                if(Kitem[j].parent_id == Kitem[i].id){
                                    console.log(Kitem[i],2)
                                    if(Kitem[i].level != 1){
                                       c_id = Kitem[i].parent_id
                                    }else{
                                          c_id = Kitem[i].id
                                    }
                                }
                             }
                        }else{
                            c_id = Kitem[j].id
                        }
                    }
                }
            }
            res.data.map(item => {
                if (item.parent_id == c_id ){
                    item.Thlevel = []
                    headLiet.push(item);
                }
            });
            for(var i = 0 ; i < headLiet.length;i++ ){
                for(var j=0; j< res.data.length;j++ ){
                    if(res.data[j].parent_id == headLiet[i].id){
                        headLiet[i].Thlevel.push(res.data[j])
                    }
                }
            }
            this.children = headLiet;
                
          
          

        },
       async Categors() {//请求数据接口 
            let res = await categoryWithSpecById({
                ID:this.category_id,
            })
           
           // this.children = res.children
            this.specslist = res.specs
            this.specslist.map(item => {
                item.kmane ="请选择"
            })
          //  console.log(res)
       },
        onRead(file) {
            let param = new FormData(); //创建form对象
            param.append('images',file.file);//通过append向form对象添加数据
            param.append('model',"products");
            // 添加请求头
            axios.post('http://192.168.0.160/api/images',param,config)
            .then(response=>{  
                console.log(response)
                if(this.type == 0){
                    this.images.push(response.data)
                }else{
                    this.credentials.push(response.data)
                } 
            })
        },
        async Relese(){
             this.condition = []
            if(this.product_name == '')   this.condition.push("请添加商品标题")
            if(this.description =='')    this.condition.push("请添加商品描述")
            if(this.images.length < 4) this.condition.push("商品图片不能低于4张")
            if(this.price =='')     this.condition.push("请填写商品价格")
            if(this.weight =='')   this.condition.push("请填写商品重量")
            if(this.keyname.id == '')  this.condition.push("请选择商品分类")
            if(this.specs.length < 1)  this.condition.push("请选择至少选择一个商品属性")
            let size = []
            if(this.size != '') {
                size.push(this.size)
                 if(this.size1 != ''){
                   size.push(this.size1)
                    if(this.size2 != ''){
                        size.push(this.size2)
                    }
                 } 
            }
            
            if(this.condition.length >= 1){ 
                this.Subfide = true
                return
            }else{
                let aaa = []
                console.log(this.specs)
                this.specs.map(item => {
                 
                    aaa.push(`{"${item.specId}":${item.specValueId}}`)
                })
                let  images = []
                let credentials = []
                this.images.map(item=>{ images.push(item.file_path)   })
                this.credentials.map(item=>{  credentials.push(item.file_path)  })
                let res = await  Addpro({
                    product_name:this.product_name,
                    description:this.description,
                    images:images,
                    promise:["自然光拍摄"],
                    credentials:this.credentials,
                    price:this.price,
                    weight:this.weight,
                    category_id:this.keyname.id,
                    hasGold:this.hasGold,
                    size:size,
                    specs:this.specs
                })
                .then(res=>{
                         this.$router.push({  path: "/mySpread",});
                })
               .catch(err=>{Toast('网络异常，请稍后再试');})
               
           }
        } 
 
  }
};
