import XHeader from '../common/header.vue'
import { Search , List} from 'vant';
import { GetOrder  } from "@/api/shop";
export default {
  name: "shopOrder",
  components:{
      XHeader, 
      Search,
      List
  },
  data() {    
    return{ 
       value:"",
       active:0,
       active1:0,
       box1:[{name:"全部",type:"all"},
           {name:"进行中",type:""},
           {name:"交易完成",type:"completed"},
           {name:"交易关闭",type:"closed"},
           ],
        box2:[{name:"待发货",type:"wait_delivery"},
           {name:"待付款",type:"wait_pay"},
           {name:"待收货",type:"wait_received"},
           {name:"鉴赏期",type:"received"},
           {name:"退货退款",type:"refund"},
        ],
        fide1:false,
        orderType:"all",
        loading: false,
        finished: false,
        pages: 1,
        total_pages: 1,
        OList:[]
    };
  },
 created() {
    this.orderList()
 },
methods: {
    goBack(){
        this.$router.go(-1);
    },
    goright(){
       this.$router.push({ path: "/site"});
    },
    // onSearch(){ },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          // 加载状态结束
          this.orderList();
        }
        this.loading = false;
        // 数据全部加载完成
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
    boxk(item,index){
        this.OList =[]
        this.pages = 1
       this.active = index
       if(index == 1){
            this.orderType = this.box2[this.active1].type
            this.fide1 = true
       }else{
           this.fide1 = false
            this.orderType = item.type
       }
        this.orderList();
    },
    boxk1(item,index){
        this.OList =[]
        this.pages = 1
        this.active1 = index
        this.orderType = item.type
        this.orderList();
    },
    async orderList(type){
            let res = await GetOrder({
                type:this.orderType,
                page: this.pages,
            })
            this.total_pages = res.meta.pagination.total_pages;
            this.OList =this.OList.concat(res.data)  
            console.log(res)
            this.OList.map(item=>{
                item.ordkey = 1
                // 待付款 wait_pay
                // 待发货 wait_delivery
                // 待收货 wait_received

                // 已收货 received
                // 退货退款 refund
                // 交易完成 completed
                // 交易关闭 closed
                if(item.order_status == "wait_pay"){item.ordkey ="待付款"}  
                else if(item.order_status == "wait_delivery"){item.ordkey ="待发货"}  
                else if(item.order_status == "wait_received"){item.ordkey ="待收货 "} 
                else if(item.order_status == "received"){item.ordkey ="已收货"} 
                else if(item.order_status == "refund"){item.ordkey ="退货退款"} 
                else if(item.order_status == "completed") {item.ordkey ="交易完成"} 
                else{item.ordkey ="交易关闭"} 
            })
    },
    Deliver(item){
           this.$router.push({ path: "/Logistics",query:{id:item.id,key:1}});  
    }
},
  
    
   
   
  
};
