import {  List} from "vant";

import XHeader from '../common/header.vue'
import XScreen from '../common/Screen.vue'
import {queryList} from "@/api/user";
export default {
  name: "DetaChoice",
  components:{
      XHeader, 
      XScreen,
      List
  },
  data() {    
    return{ 
        contEmpty:true,
        Screen:false,
        Isactive:0,
        Chullist:["推荐","最新上架","价格","筛选"],
        Objress: this.$storage.getSession('parameter'),
        productList:[],
        order_price:"",// "asc desc" 
        order_time:"",// "asc desc" 
        Edid:{
            specs:[],
            category_id:"",
            min_price:"",
            max_price:"",
            min_weight:"",
            max_weight:"",
            min_inner_diameter:"",
            max_inner_diameter:"",
            recommend:"",
            order_price:"",
            order_time:"",
        },
        loading: false,
        finished: false,
        pages:1,
        total_pages:1,
    };
  },
   created(){
        let query = this.$storage.getSession('parameter')
        let aaa =[]
        aaa[this.Objress.Choice.specs] =[this.Objress.Choice.specs1]
        let bbb =[]
        for(var i=0;i<aaa.length;i++){
            if(aaa[i] !== undefined && aaa[i] !=="" && aaa[i] !== null){
               bbb.push(aaa[i])
            }
        }
        this.Edid.specs = bbb
        this.Edid.category_id = this.Objress.Choice.catid
        this.complete()
   },
  methods: {
   
    goBack(){
        this.$router.go(-1);
    },
     onLoad() {
      // 异步更新数据
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          // 加载状态结束
          this.complete();
        }
        this.loading = false;
        // 数据全部加载完成
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
  
    async complete(){
        let res = await queryList({
            specs:JSON.stringify(this.Edid.specs),
            category_id:this.Edid.category_id,
            min_price:this.Edid.min_price, 
            max_price:this.Edid.max_price,
            min_weight:this.Edid.min_weight,
            max_weight:this.Edid.max_weight,  
            min_inner_diameter:this.Edid.min_inner_diameter,
            max_inner_diameter:this.Edid.max_inner_diameter,
            recommend:this.Edid.recommend,
            order_price:this.Edid.order_price, 
            order_time:this.Edid.order_time,
            page:this.pages
        });
         console.log(res.data,"@@@@@@@@@@@@@@@")
        this.productList = this.productList.concat(res.data)
        if( this.productList == ''){ this.contEmpty = false }
        this.total_pages = res.meta.pagination.total_pages
    },
    Chlist(item,index){
       this.Isactive = index  
       this.productList = []
       this.pages = 1
       if(item == "筛选"){
           this.Screen =true
           document.documentElement.style.overflowY = 'hidden'; 
       }else if(item == "最新上架"){
           this.Edid.order_time ="asc"
            this.Edid.order_price =""    
       }else if(item=="价格"){
            this.Edid.order_time =""
            this.Edid.order_price ="desc"      
       }else{
            this.Edid.order_time =""
            this.Edid.order_price =""     
       }
       this.complete()
    },
    showEdit(Edid){
        this.Screen =false
        this.Edid = Edid
        document.documentElement.style.overflowY = 'scroll'; 
        this.pages = 1
        this.productList = []
        this.complete()
    },
    NOScreen(){
        this.Screen =false
       document.documentElement.style.overflowY = 'scroll'; 
    }, 
    DetaChoice(id){
        let commodity = {}
        commodity.Pid =id
        this.$storage.setSession('commodity', commodity) 
        this.$router.push({path: "/Details"});
    }
     
  }
};
