import axios from '@/libs/api.request'
const baseURL='http://giggsgame.com'
//shop.jigesi.test  giggsgame.com
//http://192.168.0.160 authorizations
let include = 'items.product,shop'
export const login=data=>{
    return axios.request({
        url:`/api/authorizations`,
        data,
        baseURL,
        responseType:"json",
        method:'post',
    })
};
//登录
export const User=data=>{
    return axios.request({  
        url:`/api/users`,
        data,
        baseURL,
        responseType:"json",
        method:'post',
    })
};
//获取用户信息
export const Users=data=>{
    return axios.request({
        url:`/api/users?include=shop`,
        data,
        baseURL,
        responseType:"json",
        method:'GET',
    })
};
//修改用户信息
export const modifyuser=data=>{
    return axios.request({
        url:`/api/users`,
        data,
        baseURL,
        responseType:"json",
        method:'PATCH',
    })
};
//退出登录
export const Out=data=>{
    return axios.request({
        url:`/api/authorizations/current`,
        data,
        baseURL,
        responseType:"json",
        method:'delete',
    })
};
//获取验证码 
export const Verification=data =>{
    return axios.request({
        url:`/api/verificationCodes`,
        data,
        baseURL,
        responseType:"json",
        method:'post',
    })
};

//收获地址列表
export const Addlist=data =>{
    return axios.request({
        url:`/api/addresses?page=${data.Page}`,
        data,
        baseURL,
        responseType:"json",
        method:'get',
    })
};
//添加收获地址
export const Toaddress=data =>{
    return axios.request({
        url:`/api/addresses`,
        data,
        baseURL,
        responseType:"json",
        method:'post',
    })
};
//修改收获地址
export const Modifyaddress=data =>{
    return axios.request({
        url:`/api/addresses/${data.ID}`,
        data,
        baseURL,
        responseType:"json",
        method:'PUT',
    })
};
//删除收获地址
export const Delet = data =>{
    return axios.request({
        url:`/api/addresses/${data.ID}`,
        data,
        baseURL,
        responseType:"json",
        method:'DELETE',
    })
};
//设为默认地址 default
export const Default = data =>{
    return axios.request({
        url:`/api/addresses/${data.ID}/default`,
        data,
        baseURL,
        responseType:"json",
        method:'PUT',
    })
};
