return
}
let que =  this.$route.query
let  credentials = []
que.credentials.map(item=>{
credentials.push(item.file_path)
})
console.log(credentials)
let main_products =[]
que.main_products.map(item=>{
main_products.push(item.file_path)
})
let res = await keyEvaluat({
business_year:que.business_year,
business_category:que.business_category,
direction:que.direction,
credentials:credentials,
main_products:main_products,
avatar:this.avatar.file_path,
shop_name:this.shop_name,
province:this.value1[0],
city:this.value1[1],
address:this.address
})
if(res !=''){
this.$router.push({ path: "/ApplyState",query:{id:0}});
}

