import { login ,Verification ,User ,Users} from "@/api/user";
import Cookie from "js-cookie";

export default {
  name: "home",
  components: {},
  data() {
    return {
      Rname: "18121184205",//18959228419 17675712834
      timename:"获取验证码",
      count: '',
      timer: null,
      Verificat:""
    };
  },
  mounted() {},
  created() {
    //去除 url 中 CODE
    let code = this.GetQueryString("code");
     //Cookie.set("token", "verificationCode_7UZn9OPfV08PWvC");
    if(code){
        console.log("请求回调地址");
        localStorage.setItem('token',JSON.stringify(code));
    }
  },
  methods: {
    //解析URl
    GetQueryString(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null;
    },
    //登录 
    async logins() { 
      let res = await User({
        verification_key: this.Verificat,
        verification_code: "1234"
      });
      console.log(res,'登录')
      if (res.access_token) {
         localStorage.setItem('token',JSON.stringify(res.access_token));
      
        this.personal()
      }
    },
    //获取用户信息   401未授权 phone
    async personal() { 
         console.log("11111111111")
        let res = await Users({});
        console.log(res,'获取到用户信息')
        if(res.phone){
           this.$router.push({ path: "/" });
        }
    },
    //微信登录
    WX() {
      window.location.href ="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx798d2a32aa4275a7&redirect_uri=http://jigesi.com&response_type=code&scope=snsapi_userinfo&connect_redirect=1#wechat_redirect";
    },
   //获取验证码
   async getcode(){
     var reg=/^((13[0-9])|(14[5,7])|(15[0-3,5-9])|(17[0,3,5-8])|(18[0-9])|166|198|199|(147))\d{8}$/;
      const TIME_COUNT = 60;
      if(this.timename != "获取验证码"){
         return 
      }else if(!reg.test(this.Rname)){
        alert("请输入正确的手机号码")
        return
      }else{
        console.log('11111')
           this.count = TIME_COUNT;
           this.timer = setInterval(() => { 
              if (this.count > 0 && this.count <= TIME_COUNT) {
                    this.count--;
                    this.timename =  this.count + 'S'                    
                }else {
                  this.count = ""
                  this.timer = null
                  this.timename = "获取验证码"
                  return;
              }
          },1000)   
          let res = await Verification({
              phone: this.Rname,
          })
          if(res.key){
            console.log(res.key)
             this.Verificat = res.key 
          }
       }
    }
  }
};
