import XHeader from "../common/header.vue";
import { List } from 'vant'
import { cartList , cartsDate , Category ,queryList } from "@/api/user";
export default {
  name: "cartList",
  components: {
    XHeader,
    List
  },
  data() {
    return {
        list:[],
        contEmpty:true,
        Alllist:[],
        loading: false,
        finished: false,
        pages:1,
        total_pages:1,
        categoryid:"",
        
    };
  },
  created() {
    this.cart();
    this.comete()
  },
  
  methods: {
    goBack() {
      this.$router.go(-1);
    },
     onLoad() {
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          this.comete();
        }
        this.loading = false;
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
     async comete() {//请求数据接口 
        let bbb =[]
        let res = await queryList({
            specs:JSON.stringify(bbb),
            recommend:"",
            order_price:"", 
            order_time:"",
            category_id:this.categoryid,
            min_price:"", 
            max_price:"",
            min_weight:"",
            max_weight:"",  
            min_inner_diameter:"", 
            max_inner_diameter:"",
            page:this.pages
        });
        console.log(res,"@@@@@@@@@@@@@@@@@@@@@@@")
         for(var i = 0; i< res.data.length ; i ++){
             for(var j=0;j<res.data[i].images.length ; j++){
                 res.data[i].images[j] = res.data[i].images[j].replace(/shop.jigesi.test/,'192.168.0.160')
             }
        }
        this.Alllist = this.Alllist.concat(res.data) 
        if( this.Alllist == ''){ this.contEmpty = false }
        this.total_pages = res.meta.pagination.total_pages
    },
    async cart() {
      //加入购物车
        let res = await cartList({});
        res.map(item=>{
            item.active = false
            item.isactive = false
            item.outprice = 0
            item.outsize = 0
            item.map(key=>{
                key.active = false;
            })
        })
        this.list = res
    },
    Settlement(item){
       if(item.outprice !== 0){
           let prfiole = {
                orderType:1,
                outprice:item.outprice,
                data:[]
           }
          
           item.map(page=>{
              if(page.active){
                 prfiole.data.push(page)
              }
           })
          
           console.log(prfiole,"@@@@@@@@@@@@@@@@")
           this.$storage.setSession('SureItem', prfiole) 
           this.$router.push({path: "/Sure"});
       }
    },
    toswitch(item,index){
        this.list = [...this.list]
        item.outprice = 0
        item.active =! item.active
        if(item.active){           
            item.map(key=>{
               key.active = true;
               item.outsize ++
               item.outprice += parseFloat(key.product.price)
            })
            item.isactive = true
        }else{
            item.map(key=>{
               key.active = false;
               item.outsize = 0
               item.outprice = 0
            })
            item.isactive  =false
        }
        item.outprice =  parseFloat(item.outprice).toFixed(2)
    },
    talswitch(item,index,type,dex){
        this.list = [...this.list]
        type.active =! type.active
        item.active = false
        item.outsize = 0
        item.outprice  = 0
        item.map(key=>{
            if(key.active){
               item.outsize ++
               item.outprice += parseFloat(key.product.price)
            }
              
        })  
        item.outprice =  parseFloat(item.outprice).toFixed(2)
        if(item.outsize < 1) item.isactive  =false
        else item.isactive  =true
    },
    async date(item,index,type,dex){//删除
       let res = await cartsDate({
             ID:type.id,
        })
        this.list = [...this.list]
        if(res !=""){
            if(item.length > 1 ){
                 item.splice(dex,1)
            }else{
                if(this.list.length > 1){
                    this.list.splice(index,1)
                }else{
                    this.list = ""
                }
             }
        }
    },
     information(item) { //店铺信息
     
      this.$storage.setSession("shopInform",item[0].product.shop_id);
      this.$router.push({  path: "/shopInform"});
    },
  }
};
