import XHeader from "../common/header.vue";
import { List } from 'vant';
export default {
    name:"Budget",
    components:{
      XHeader
    },
    data () {
        return {
            dataList:false,
            img1:true,
            img2:true,
            isactive:0,
            key:0,
            typename:"全部",
            keyname:"全部",
            typelist:["全部","商品订单","求购竞标","赞赏","拍卖保证金","拍卖违约金","提现"],
            keylist:["全部","收入","体现"],
            list:[]
         }
    },
    created(){ 
          let id = this.$route.query.id
          if(id == 2){
              this.typename ='提现'
              this.isactive = 6
              
            }
    },
    methods: {
        goBack() {
          this.$router.go(-1);
        },
        list_key(key){
            let name 
            if(key == 0){
                this.key = 0
                this.list = this.typelist
                this.img1 =false ;
                 this.img2 = true
                 name = this.typename
            }else{
                 this.key = 1
                this.list = this.keylist
                 this.img1 =true ;
                this.img2 = false
                name = this.keyname
            }
            for(var i =0;i< this.list.length;i++){
                if(name == this.list[i]) this.isactive = i  
            }
            this.dataList = true
            document.documentElement.style.overflowY = "hidden";
        },
       Close(){
             this.dataList = false
             document.documentElement.style.overflowY = "scroll";
       },
       select(item,index){
           console.log(item)
           this.isactive = index
           if(this.key == 0) {this.typename = item ; }
           else { this.keyname = item  ; }
           this.dataList = false
            document.documentElement.style.overflowY = "scroll";
            this.img1 = this.img2 = true
       }
    }
}
