import XHeader from '../common/header.vue'
import Addsite from '../common/Addsite.vue'
import { List } from 'vant'
import Cookie from "js-cookie"
import { Retreat ,DeRetreat} from "@/api/shop"
import axios from "axios"
export default { 
  name: "Address",
  components:{
      XHeader,
      Addsite,
      List
  },
  data() {    
    return{ 
        Edit:false,
        Objress:null,
        AddReslist:[],
        loading: false,
        finished: false,
        list1: 1 ,
        total_pages:null,
    };
  },
  created(){
     this.addor()
  },
  methods:{
        onLoad() { //上啦加载
            // 异步更新数据
            setTimeout(() => {
                if(this.total_pages != 1 &&  this.total_pages != 1){
                   this.list1 ++
                   this.addor()
                }
                // 加载状态结束
                this.loading = false;
                // 数据全部加载完成
                if (this.list1 >= this.total_pages){
                   this.finished = true;
                }
            },2000);
        },
        async addor(item,index){//获取地址列表
            let res = await Retreat({
                Page:this.list1
             });
            this.total_pages = res.meta.pagination.total_pages
            console.log(res)
             this.AddReslist =this.AddReslist.concat(res.data) 
            // console.log(this.AddReslist)
        },
        goBack(){
            this.$router.go(-1);
        },  
        OutAdd(){//子组件关闭
            this.Edit = false
        },
        Submission(item,key){//子组件点击确认
           this.Edit = false
           if(key == 0){
               this.AddReslist[item.index] = item
               return
           }else{
               
                this.AddReslist.unshift(item)
           }
           
            
        },
        onAdd() { //添加收获地址
            this.Edit = true
            this.Objress = undefined
        },
        //点击编辑收货地址
        onEdit(item, index) {
            item.index = index
            this.Objress = item
            this.Edit = true
            
        },
        //删除订单
       async Delete(item,index){
            let res = await DeRetreat({
                 ID:item.id
             });
             console.log(res)
            this.AddReslist.splice(index,1)
        },
    }
};
