import XHeader from '../common/header.vue'
import {  deliverGoods } from "@/api/shop";
export default { 
  name: "Logistics",
  components:{
      XHeader,
     
  },
  data() {    
    return{ 
       expressno:5117441541215211,
       active:0,
       LogList:["EMS","申通","顺丰快递","圆通快递","韵达快递","中通速递","EMS","申通","顺丰快递","圆通快递","韵达快递","中通速递"]
    };
  },
  created(){
    
  },
  methods:{
       Swite(item,index){
          this.active = index 
       },
        goBack(){
            this.$router.go(-1);
        },  
        async goright(){
            if(this.expressno != ''){
                let key = this.$route.query.key 
                let id  = this.$route.query.id
                if(key == 1){
                    let res = await deliverGoods({
                        ID:id,
                        express_company:this.LogList[this.active],
                        expressno:this.expressno
                    }) 
                    console.log(res)
                }
            }else{
                console.log('检查快递信息')
            }
             
        }
    }
};
