import XHeader from '../common/header.vue'
import AddressEdit from '../common/AddressEdit.vue'
import { Toast } from 'vant';
import { Addlist , orderst } from "@/api/user";
export default { 
    name:"Sure",
    components:{
       XHeader,
       AddressEdit
    },
    data(){
        return{     
            Edit:false,
            //
            SureItem:{},   
            addorlist:{},
            addtype:false,
            Objress:{},
            ims:1,
            remark:"",
            orderType:0,
        }
    },
    created(){ 
        let quer= this.$storage.getSession('SureItem') 
        if(quer.orderType != 0){
            quer.price = quer.outprice
        } 
        this.SureItem = quer
        console.log( this.SureItem)
        this.orderType = quer.orderType
        this.addor()
    },
    methods:{
        goBack(){
            this.$router.go(-1); 
        },
        async OrDeta(){
            let a=[]
            if(this.orderType == 0){
                let item  = {
                    product_id:this.SureItem.id
                }
                a.push(item)  
            }else{
                for(var i= 0;i<this.SureItem.data.length;i++){
                    let item  = {
                        product_id:this.SureItem.data[i].product_id
                    }
                    a.push(item) 
                }
            }           
            let res = await orderst({
                address_id:this.addorlist.id,
                items:a,
                remark:this.remark
            }).then(res=>{
                let id = res.id
                this.$router.push({path: "/OrderDeta",query:{id:id}});
            }).catch(err=>{
                console.log(err.response.data)
                if(err.response.data.status_code == 400){
                    Toast.fail('已近售出');
                    setTimeout(() => { 
                       this.$router.go(-1); 
                    },3000)   
                   
                }
            })
           
        },
        async addor(){//获取地址列表
            let res = await Addlist({
               
             });
            console.log(res)
            if(res.data.length >= 1){
                this.addorlist = res.data[0]
                this.addtype = true
            }else{
                console.log("需要添加收货地址")
            }
        },
        Adds(){//添加收货地址
           this.Edit = true
           let item = "Sure"
           this.Objress = null
        },
        addChoice(){
            this.$router.push({path: "/Address",query:{Address:1123}});
        },
        OutAdd(){
           this.Edit = false
        },
        Submission(item,key){
           this.Edit = false
           this.addor()
        }
    }
}
