import XScreen from "./common/Screen.vue";
import todoList from "./common/Bott-comp.vue";
import { Tab, Tabs, Swipe, SwipeItem , List, Search, Toast} from "vant";
import { Category, queryList ,cartList } from "@/api/user";
export default {
  name: "HelloWorld",
  components: {todoList,XScreen, Tab, Tabs, Swipe,SwipeItem,List,Search,Toast},
  data() {
    return {
      vehiclet:0,
      contEmpty:true,
      value: "",
      active: 0,
      type: "A",
      Token: "1",
      Screen: false,
      Navlist: [ { product_name: "推荐", id: 0, level: 1, parent_id: 0, sort: 0 }],
      images: [require('../assets/img/shop/5.png'),require('../assets/img/shop/6.png'),require('../assets/img/shop/7.png')],
      Detaed: [],
      Retype: [
        { name: "官方直营", key: "平台鉴定 品质甄选",img:require('../assets/img/shop/5.png'),img1:require('../assets/img/shop/6.png') },
        { name: "品牌店铺", key: "发现口碑好店",img:require('../assets/img/shop/7.png'),img1:require('../assets/img/shop/8.png')},
        { name: "每日上新", key: "24小时持续上新",img:require('../assets/img/shop/1.png'),img1:require('../assets/img/shop/2.png')},
        { name: "排行榜", key: "人气宝贝 每日0点更新" ,img:require('../assets/img/shop/3.png'),img1:require('../assets/img/shop/4.png')}
      ],
      Categorylist: [],
      categoryid: "",
      Alllist: [],
      loading: false,
      finished: false,
      pages: 1,
      total_pages: 1,
      resd: null,
      Edid: {specs: [],  min_price: "",max_price: "", min_weight: "",max_weight: "", min_inner_diameter: "",
             max_inner_diameter: "",recommend: "", order_price: "", order_time: ""
            },
      Objress: { type: "",page: 0, Choice:{catid: 1} }
    };
  },
  created() {
   // Toast.loading({ mask: true,message: '加载中...'});
    this.Categorys(); //分类列表
     this.comete(); //商品列表
     this.cart()
  },
  methods: {
    onLoad() { // 异步更新数据
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          this.comete();  // 加载状态结束
        }
        this.loading = false; // 数据全部加载完成
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      },2000);  
    },
    onSearch() { //搜索
      Toast.fail("升级中..."); 
    },
    async cart() {
      //加入购物车
        let res = await cartList({});
        
        res.map(item=>{
           item.map(key=>{
              this.vehiclet  ++ 
           })
        })
          
    },
    async Categorys() {  //分类列表
      let res = await Category({});
      this.Categorylist = res.data; //level:一级导航 {2级 parent_id } <==>{1级 = ID}
      let headLiet = [];
     
      res.data.map(item => {
        if (item.level == 1) headLiet.push(item);
      });
      this.Navlist = this.Navlist.concat(headLiet);
    },
    async comete() {//商品列表
      let res = await queryList({
              specs: JSON.stringify(this.Edid.specs),
              recommend: this.Edid.recommend,
              order_price: this.Edid.order_price,
              order_time: this.Edid.order_time,
              category_id: this.categoryid,
              min_price: this.Edid.min_price,
              max_price: this.Edid.max_price,
              min_weight: this.Edid.min_weight,
              max_weight: this.Edid.max_weight,
              min_inner_diameter: this.Edid.min_inner_diameter,
              max_inner_diameter: this.Edid.max_inner_diameter,
              page: this.pages
      });
       console.log(res.data,"2222222222222222")
        this.Alllist = this.Alllist.concat(res.data);
        this.total_pages = res.meta.pagination.total_pages;
        if( this.Alllist == ''){ this.contEmpty = false }
    },
    showEdit(item) {
      document.documentElement.style.overflowY = "scroll";
      this.Alllist = [];
      this.page = 1;
      this.Screen = false;
      this.Edid = item;
      this.comete();
    },
    NOScreen() {
      this.Screen = false;
      document.documentElement.style.overflowY = "scroll";
    },
    scrEN() {
      this.Screen = true;
      this.Objress.Choice.catid = this.Navlist[this.active].id;
      document.documentElement.style.overflowY = "hidden";
    },
    change() {
      this.Alllist = [];
      let Edid = {  specs: [],min_price: "", max_price: "",min_weight: "",max_weight: "",
                    min_inner_diameter: "",max_inner_diameter: "",recommend: "", order_price: "", order_time: ""
      };
      this.Edid = Edid;
      this.pages = 1;
      let list = [];
      if (this.active == 0) {
        this.type = "A";
        this.categoryid = "";
     
      }else {
        this.type = "B";
        this.categoryid = this.Navlist[this.active].id;
        let level = this.Navlist[this.active].id;
        this.Categorylist.map(item => {
          if (item.parent_id == level) list.push(item);
        });
        this.Detaed = list;
      }
      this.comete();
    },

    link(item, index) {
      let type = this.Navlist[this.active];
      if(index == 0 || index == 1) {
       // this.$router.push({ path: "/Official" });
        Toast.fail("暂未开通..."); 
      } else if (index == 2) {
        let parameter = { type: "每日上新", key: type };
        this.$storage.setSession("Everyday", parameter);
        this.$router.push({ path: "/Everyday"});
      } else if (index == 3) {
        let parameter = { type: "排行榜", key: type };
        this.$storage.setSession("Everyday", parameter);
        this.$router.push({ path: "/Everyday"});
      }
    },
    //单独点击每日上新
    Newest() {
      let type = this.Navlist[this.active];
      let parameter = { type: "每日上新", key: type };
      this.$storage.setSession("Everyday", parameter);
      this.$router.push({ path: "/Everyday"});
    },
    //单独点击排行榜
    Rankings() {
      let type = this.Navlist[this.active];
      let parameter = { type: "排行榜", key: type };
      this.$storage.setSession("Everyday", parameter);
      this.$router.push({ path: "/Everyday"});
    },
    //点击查看全部
    Deta_type() {
      let type = this.Navlist[this.active];
      let parameter = { key: type };
      this.$router.push({ path: "/Detatype", query: parameter });
    },
    //点击进入分类列表页
    DetaChoice(item) {
      let Choice = {};
      Choice.catid = item.id; //筛选 自身ID
      let parameter = { type: item, page: 0, Choice: Choice };
      this.$storage.setSession("parameter", parameter);
      this.$router.push({ path: "/DetaChoice" });
    },
    //点击进入商品详情
    Details(id) {
      let commodity = {};
      commodity.Pid = id;
      this.$storage.setSession("commodity", commodity);
      this.$router.push({ path: "/Details" });
    }
  }
};
