import { Tabbar, TabbarItem } from 'vant';
export default {
  name: "bottComp",
  components: {
    Tabbar,
    TabbarItem
  },
  data() {
    return {
      isActive:this.childCom,
      bottlist: [
        { name: "首页", type: "0",url:"/" ,img:require("@/assets/img/bottom/s1.png"), img2:require("@/assets/img/bottom/s.png")},
        { name: "拍卖", type: "2",url:"/Auction" ,img:require("@/assets/img/bottom/p1.png"), img2:require("@/assets/img/bottom/p.png")},
        { name: "消息", type: "3",url:"/News" ,img:require("@/assets/img/bottom/x1.png"), img2:require("@/assets/img/bottom/x.png")},
        { name: "我的", type: "4",url:"/My" ,img:require("@/assets/img/bottom/m1.png"), img2:require("@/assets/img/bottom/m.png")}
      ]
    };
  },
  props:{
    childCom: Number //这里指定了字符串类型，如果类型不一致会警告的哦
  },
  methods: {
      listtype(url){
          this.$router.push({path: url});
      }     
  }
}; 
