import XHeader from '../common/header.vue'
import AddressEdit from '../common/AddressEdit.vue'
import { List } from 'vant' 
import Cookie from "js-cookie"
import { Addlist , Delet , Default } from "@/api/user"
import axios from "axios"
export default {
  name: "Address",
  components:{
      XHeader,
      AddressEdit,
      List
  },
  data() {    
    return{ 
        Edit:false,
        Objress:null,
        AddReslist:[],
        loading: false,
        finished: false,
        list1: 1 ,
        total_pages:null,
    };
  },
  created(){
     this.addor()
  },
  methods:{
        onLoad() { //上啦加载
            // 异步更新数据
            setTimeout(() => {
                if(this.total_pages != 1 &&  this.total_pages != 1){
                   this.list1 ++
                   this.addor()
                }
                // 加载状态结束
                this.loading = false;
                // 数据全部加载完成
                if (this.list1 >= this.total_pages){
                   this.finished = true;
                }
            },2000);
        },
        async addor(item,index){//获取地址列表
            let res = await Addlist({
                Page:this.list1
             });
            this.total_pages = res.meta.pagination.total_pages
            console.log(res)
            this.AddReslist =this.AddReslist.concat(res.data) 
            console.log(this.AddReslist)
        },
        goBack(){
            this.$router.go(-1);
        },
        //打开子组件
        onAdd() {
            this.Edit = true
            this.Objress = undefined
        },
        OutAdd(){//子组件
            this.Edit = false
        },
        Submission(item,key){//子组件点击确认
           this.Edit = false
           if(key == 0){
               this.AddReslist[item.index] = item
               return
           }else{
               this.AddReslist =[]
                this.list1 = 1
                this.addor()
           }
           
            
        },
        //点击编辑收货地址
        onEdit(item, index) {
            item.index = index
            this.Objress = item
            this.Edit = true
            //Toast('编辑地址:' + index);
        },
        //删除订单
       async Delete(item,index){
            let res = await Delet({
                 ID:item.id
             });
             console.log(res)
            this.AddReslist.splice(index,1)
        },
        async fault(item,index){//设为默认地址
             let res = await Default({
                 ID:item.id
             });
             if(res != ''){
                this.AddReslist.splice(index,1)
                this.AddReslist.unshift(res) 
                if(this.$route.query.Address == 1123){
                     this.$router.go(-1);
                }
             }
            
        }
    }
};
