import XHeader from '../common/header.vue'
import { shopinform } from "@/api/shop";
import { Toast } from 'vant';
export default {
  name: "mySpread",
  components:{
      XHeader,
      Toast
  },
  data() {    
    return{ 
      mySpread_list:["商品管理","订单管理","钱包"], //"拍品管理","专场管理","优惠卷"
      fromdata:{}
    };
  },
  created(){ 
      this.forem()
  },
  methods: {
    goBack(){
        this.$router.go(-1);
    },
    async forem(){
        let res = await shopinform({
            ID:this.$route.query.id
        })
        this.fromdata = res
        console.log(res,"@@@@@@@@@@@@@@@")
    },
    goright(){
        this.$router.push({ path: "/jadeType"});
    },
    mysed(item){
        console.log(item)
        if(item == "订单管理"){
            this.$router.push({ path: "/shopOrder"});
        }else if(item == "商品管理"){
             this.$router.push({ path: "/Administration"});
        }else{
            Toast('系统升级管理中...........');
        }
    },
    Guarantee(){
       this.$router.push({ path: "/Guarantee"});
    },
    Business(item){
        this.$router.push({ path: "/Business",query:{name:item}});
    },
    Extension(){//推广营销
         this.$router.push({ path: "/Extension"});
    }
  }
};
