import XHeader from "../common/header.vue";
export default {
    name:"wallet",
    components:{
      XHeader
    },
    data () {
        return {
            price:"0",
            isActive:false,
            
         }
    },
    created(){ 
   
    },
    methods: {
        goBack() {
          this.$router.go(-1);
        },
        right(dex){
                 this.$router.push({ path: "/Budget",query:{id:dex}});        
        },
        compete(dex){
            this.$router.push({ path: "/compete",query:{id:dex}});     
        }
      
    }
}
