import XHeader from './common/header.vue'
import { ordersPay } from "@/api/user";
export default {
  name: "Payment",
  components: {
      XHeader
  },
  data() {
    return {
        active:0,
        payment:[
            {name:"微信支付",page:"单笔最高 ￥5,000 - 50,000",img:"weix.png"},
            {name:"支付宝支付",page:"单笔最高 ￥5,000 - 250,000",img:"zfb.png"},
            {name:"余额支付",page:"剩余 ￥5,000",img:"yue.png"}
        ]
    }
  },
  created(){ 
        let payment = this.$route.query;
        console.log(payment)
    },
  methods:{
    goBack(){
       
        this.$router.go(-1);
    },
    async paymoney(){
         let res= await ordersPay({
            ID:this.$route.query.id
         })
         if(res){
            console.log("支付成功")
            if(this.$route.query.goback == 1){
               this.$router.go(-1);
            }else{
                this.$router.push({path:"/"});
            }
           
         }
    }
   
  }
