import XHeader from '../common/header.vue'
import { Rate, Swipe, SwipeItem , Toast } from "vant";
import { shopinform , shoplist , Evaluate} from "@/api/shop";
import XScreen from "../common/Screen.vue";
export default {
    name:"Sure",
    components:{
       XHeader, Rate, Swipe, SwipeItem , Toast ,XScreen
    },
    data(){
        return{     
            active:0,
            isactive:0,
            value: 5,
            disabled: true,
            inform:null,
            solltop:false,
            main:[{name:"商品",key:""},{name:"评价",key:""},{name:"动态",key:""}],
            In_main_list:["上新","已出售","价格","筛选"],
            Objress: {
              type: "",
              page:9,
              Choice: {
                catid:1
              }
            },
            lists:[],
            Screen:false,
            Edid: {
              specs: [],
              min_price: "",
              max_price: "",
              min_weight: "",
              max_weight: "",
              min_inner_diameter: "",
              max_inner_diameter: "",
              recommend: "",
              order_price: "",
              order_time: "",
              catid:"",
            },
            loading: false,
            finished: false,
            pages:1,
            total_pages:1,
            status:"shelve",// 上架shelve   sold 
        }
    },
    created(){ 
       Toast.loading({ mask: true,message: '加载中...'});
        this.shops()
        this.comete()
        //this.Evalu()
        this.add();
    },
    methods:{
        goBack(){
            this.$router.go(-1); 
        },
        onLoad() {
        // 异步更新数据
          setTimeout(() => {
            if (this.pages < this.total_pages) {
              this.pages++;
              // 加载状态结束
              this.comete();
            }
            this.loading = false;
            // 数据全部加载完成
            if (this.pages >= this.total_pages) {
              this.finished = true;
            }
          }, 2000);
        },
        async shops(){//店铺信息
            let res = await shopinform({
                ID:this.$storage.getSession('shopInform')
            })
            console.log(res)
            res.avatar = res.avatar.replace(/shop.jigesi.test/,'192.168.0.160')
            this.inform = res
            
        },
        async comete() { //店铺商品
          let res = await shoplist({
            ID:this.$storage.getSession('shopInform'),
            specs: JSON.stringify(this.Edid.specs),
            recommend: this.Edid.recommend,
            order_price: this.Edid.order_price,
            order_time: this.Edid.order_time,
            category_id: this.Edid.catid,
            min_price: this.Edid.min_price,
            max_price: this.Edid.max_price,
            min_weight: this.Edid.min_weight,
            max_weight: this.Edid.max_weight,
            min_inner_diameter: this.Edid.min_inner_diameter,
            max_inner_diameter: this.Edid.max_inner_diameter,
            status:this.status,
            page: this.pages
          });
          console.log(res,"22222222222222222222")
          this.lists   = this.lists.concat(res.data)
          this.total_pages = res.meta.pagination.total_pages 
        },

        async Evalu(){//店铺评论列表
            let res = await Evaluate({
                ID:this.$route.query.id
            })
        },
        Imain(index){
           this.active = index
        },
        Inmain(item,index){
          this.isactive = index
          if(item=="筛选"){
            this.Screen = true
            document.documentElement.style.overflowY = "hidden";
          }else {
              this.lists = []
              if(item == "上新"){
                this.status = "shelve"
                this.Edid.order_price = ""
              }else if(item == "已出售"){
                this.status = "sold"
                this.Edid.order_price = ""
              }else{
                this.status = "shelve"
                this.Edid.order_price = "asc"
              }
              this.comete()
          }
        },
        add() {
          var _this = this;
          window.addEventListener("scroll", function() {
            var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            if (scrollTop > 150) {
               _this.solltop = true;
            } else if (scrollTop < 150) {
               _this.solltop = false
            }
          });
       }, 
       NOScreen(){
           document.documentElement.style.overflowY = "scroll";
           this.Screen = false
       },
        showEdit(item){
            document.documentElement.style.overflowY = "scroll";
           this.Screen = false
           this.Edid = item
           //this.comete()
        },
        Details(id){
            this.lists = [];
            let commodity = {};
            commodity.Pid = id;
            this.$storage.setSession("commodity", commodity);
            this.$router.push({ path: "/Details" });
        },
       
    }
}
