import XHeader from '../common/header.vue'
import { details } from "@/api/user";
import { List } from 'vant'
import { ordersDelete ,ordersPatch , ordersPut , queryList } from "@/api/user";
export default {
    name:"OrderDeta",
    components:{
       XHeader,
       List,
    },
    created(){ 
        let qid = this.$route.query.id;
        this.addor()
        this.comete()
    },
    data(){
        return{
            ord_problem_list:[{name:"到货需要多久",key:2},{name:"发货运费谁出",key:0},{name:"退货费用谁出",key:1},{name:"退款多久到账",key:0},{name:"什么时候发货",key:2},{name:"到货后不满意",key:0}] , 
            orderItem:null,
            contEmpty:true,
            Alllist:[],
            loading: false,
            finished: false,
            pages:1,
            total_pages:1,
            categoryid:"",
        }
    },
    methods:{
        goBack(){
            this.$router.go(-1);
        },
        onLoad() {
            setTimeout(() => {
                if (this.pages < this.total_pages) {
                this.pages++;
                this.comete();
                }
                this.loading = false;
                if (this.pages >= this.total_pages) {
                this.finished = true;
            }
        }, 2000);
        },
        OrderDeta(){ //确认订单
            this.$router.push({path:"/Payment"})
        },
        async comete() {//请求数据接口 
            let bbb =[]
            let res = await queryList({
                specs:JSON.stringify(bbb),
                recommend:"",
                order_price:"", 
                order_time:"",
                category_id:this.categoryid,
                min_price:"", 
                max_price:"",
                min_weight:"",
                max_weight:"",  
                min_inner_diameter:"", 
                max_inner_diameter:"",
                page:this.pages
            });
            console.log(res,"@@@@@@@@@@@@@@@@@@@@@@@")
            for(var i = 0; i< res.data.length ; i ++){
                for(var j=0;j<res.data[i].images.length ; j++){
                    res.data[i].images[j] = res.data[i].images[j].replace(/shop.jigesi.test/,'192.168.0.160')
                }
            }
            this.Alllist = this.Alllist.concat(res.data) 
            if( this.Alllist == ''){ this.contEmpty = false }
            this.total_pages = res.meta.pagination.total_pages
        },
         async addor(){//订单详情
            let res = await details({
                ID:this.$route.query.id,
             });
            if(res.order_status == "closed"){res.ordkey ="交易关闭"}  //交易关闭
            else if(res.order_status == "wait_pay"){res.ordkey ="待付款"}  //取消订单 付款
            else if(res.order_status == "wait_delivery"){res.ordkey ="待发货 "} // 崔发货
            else if(res.order_status == "wait_received"){res.ordkey ="待收货"} // 确认收货 
            else if(res.order_status == "received"){res.ordkey ="已收货"} // 申请退款 
            else if(res.order_status == 'wait_change_price'){{res.ordkey ="等待改价"}}
            else {res.ordkey ="交易完成"} //申请退款 申请换货
            this.orderItem = res 
            console.log(res,"订单详情")
        },
        async cancel(){//取消订单
                let res = await ordersPatch({
                    ID:this.orderItem.id,
                }) 
                console.log(res,"取消订单")
                if(res){
                    res.ordkey ="交易关闭"
                    this.orderItem = res 
                }
            },
        async delecl(item,index){//删除
                let res = await ordersDelete({
                    ID:this.orderItem.id,
                })
                if(res !=""){
                   this.$router.go(-1);
                }
            },
        async Sure(item,index){//确认收获
            let res = await ordersPut({
                ID:this.orderItem.id,
            })
            if(res !=""){
                console.log(res,"确认收获")
                    this.$router.go(-1);
            }
        },
        payment(){
            let payment ={
                id:this.$route.query.id,
                price:this.orderItem.total_amount
            }
            this.$router.push({path:"/Payment",query:payment});
        }, 
        Help(item){
            this.$router.push({path:"/Help"});
        },
        Help1(dex){
           this.$router.push({path:"/Help1",query:{id:dex}});
        }
    }
}
