import XHeader from "../common/header.vue";
import { Uploader } from 'vant';
import  { Evaluat , GetEvaluat } from "@/api/shop";
import Cookie from "js-cookie";
import axios from "axios";
let config = {
        headers:{
            'Content-Type':'multipart/form-data',
            'Authorization':'Bearer '+Cookie.get("token")//取出Cookie
        }
}; 
export default {
  naem: "Apply",
  components: {
    XHeader,
    Uploader
  },
  data() {
    return {
        oRead:0,
        name:null,
        phone:null,
        wechat:null,
        idcard:null,
        porimfg0:{path:"",file_path:""},
        porimfg1:{path:"",file_path:""},
        porimfg2:{path:"",file_path:""},
        imgs:0,
         
    };
  },
  created() {
     this.authentication()
  },
  methods: {
    read(key){
        this.imgs = key
    },
    close(key){
       if(key == 0){
            this.porimfg0.path = ''
        }else if(key == 1){
            this.porimfg1.path = ''
        }else{
            this.porimfg2.path = ''
        }
        console.log(this.porimfg0)
    },
    
    onRead(file) { 
        console.log(file.file,"1111111")
        let param = new FormData(); //创建form对象
        param.append('images',file.file);//通过append向form对象添加数据
        param.append('model',"idcards");
        // 添加请求头  
        axios.post('http://192.168.0.160/api/images',param,config)
        .then(response=>{
            console.log(response.data);
            if(this.imgs == 0){
              this.porimfg0 =response.data 
            }else if(this.imgs == 1){
              this.porimfg1 =response.data 
            }else{
              this.porimfg2 =response.data 
            }
            
        })     
    }, 
   
    goBack() {
      this.$router.go(-1);
    },
    async authentication(){
        let res =await GetEvaluat({
    
        })
        if(res != ''){
            if(res.real_name !=''){
                this.name = res.real_name
                this.phone = res.phone
                this.wechat = res.wechat
                this.idcard = res.id_card
                this.porimfg0.path = res.id_card_picture[0]
                this.porimfg1.path = res.id_card_picture[1]
                this.porimfg2.path = res.id_card_picture[2]
                
                this.porimfg0.file_path = res.id_card_picture_origin[0]
                this.porimfg1.file_path = res.id_card_picture_origin[1]
                this.porimfg2.file_path = res.id_card_picture_origin[2]
            }
        }
        console.log(res,"@@@@@@@@@@@@@@")
    },
    async Apply_one(){
         let phone=/^((13[0-9])|(14[5,7])|(15[0-3,5-9])|(17[0,3,5-8])|(18[0-9])|166|198|199|(147))\d{8}$/; //手机号码
         let wechat= /^[a-zA-Z]{1}[-_a-zA-Z0-9]{5,19}$/; //微信号 
         let idcard = /^([1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/; 
       if(this.name == null || this.naem == "" ){
          alert("姓名不能能为空")
          return
       }
        if(!phone.test(this.phone)){
           alert("输入正确的手机号")
            return
       }
       if(!wechat.test(this.wechat)){
           alert("输入正确的微信号")  
           return
       }
       if(!idcard.test(this.idcard)){ 
            alert("输入正确的身份证号") 
             return
       }
       if(this.porimfg0 == null || this.porimfg1 == null || this.porimfg2 == null){
           alert("请上传身份证信息")
           return
       }
        let res = await Evaluat({
           real_name:this.name,
           phone:this.phone,
           wechat:this.wechat,
           id_card:this.idcard,
           id_card_picture:[this.porimfg0.file_path,this.porimfg1.file_path,this.porimfg2.file_path]
        })
        this.$router.push({ path: "/Apply1"});   
    }
  }
};
