import XHeader from '../common/header.vue'
import { Rate , Uploader } from 'vant';
import Cookie from "js-cookie";
import axios from "axios";
import { SetRata } from "@/api/user";
let config = {
        headers:{
            'Content-Type':'multipart/form-data',
            'Authorization':'Bearer '+Cookie.get("token")//取出Cookie
        }
};
export default {
  name: "Rate",
  components:{
      XHeader,
      Rate,
      Uploader
  },
  data() {    
    return{ 
        value1:1,
        value2:1,
        shopName:"",
        querlist:[],
        Eluate:"",
        imglist:[],
        key:0,
    };
  },
 created() {
    let que =   this.$storage.getSession("Rate");
  
    que.items.data.map(item=>{
        item.imglist = []
        item.Ravalue = 1
        item.Eluate =""
    })
     this.querlist = que.items.data
       console.log(que)
 },
methods: {
    goBack(){
        this.$router.go(-1);
    },
    subscript(index){
         this.key = index
         console.log(index)
    },
    change(mark){
            if(mark < 2) this.shopName = "一般"
            else if(mark == 3) this.shopName = "好"
            else if(mark == 4) this.shopName = "很好"
            else if(mark == 5) this.shopName = "非常好"
    },
    onRead(file) {
        let param = new FormData();//创建form对象
        param.append('images',file.file);//通过append向form对象添加数据
        param.append('model',"idcard");
        // 添加请求头
        axios.post('http://shop.jigesi.test/api/images',param,config)
        .then(response=>{
            console.log(response.data);
            this.querlist  = [...this.querlist]
            this.querlist[this.key].imglist.push(response.data.path) 
        })
    },
    close(dex,index){ //删除
       console.log(dex)
       this.querlist  = [...this.querlist]
       this.querlist[dex].imglist.splice(index,1);
    }, 
    async  goright(){
        let reviews =[]
        for(var i = 0;i< this.querlist.length;i++){
            reviews.push({
                id:this.querlist[i].id,   
                describe_star:this.querlist[i].Ravalue, 
                review:this.querlist[i].Eluate,
            })
        }
        let res = await SetRata({  
           ID: this.$storage.getSession("Rate").id,
           reviews:reviews,
           service_star:this.value1,
           delivery_star:this.value2
        })
        if(res){
            console.log("评价成功")
            this.$router.push({ path: "/Order" });
        }
    }

},
  
    
   
   
  
};
