import XHeader from '../common/header.vue'
import { Tab, Tabs, Swipe, SwipeItem } from "vant";
import { List } from "vant";
import Cookie from "js-cookie";
import { orders , ordersDelete ,ordersPatch , ordersPut } from "@/api/user";
export default {
  name: "Official",
  components:{
      XHeader,
      Tab,
      Tabs, 
      Swipe,
      SwipeItem,
      List
  },
  data() {    
    return{ 
      IsActive:0,
      active: 0,
      type: "A",
      type1:"b", 
      Navlist:[
           {name:"全部",type:"all"},
           {name:"未付款",type:"wait_pay"},
           {name:"待发货",type:"wait_delivery"},
           {name:"待收货",type:"wait_received"},
           {name:"售后",type:"completed"},
        ],
      Order:[], 
      orderType:"all",
      loading: false,
      finished: false,
      pages: 1,
      total_pages: 1,
    };
  },
  created(){
      console.log(this.$route.query,"@@@@@@@@@@@@")
      if(this.$route.query.id != 5 ){
          this.active = this.$route.query.id + 1
          this.orderType = this.Navlist[this.active].type
      }
       
        this.orderList()
      
    
  },
  methods: {
    goBack(){
        this.$router.go(-1);
    },
    onLoad(){
      // 异步更新数据
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          //加载状态结束
          this.orderList();
        }
        this.loading = false;
        // 数据全部加载完成
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
    
    change(){
      this.Order = []
      // console.log(this.active) order_status 
      if (this.active == 0) {
         this.type = "A";
      }else {
         this.type = "B";
      }
      this.orderType = this.Navlist[this.active].type
      this.pages = 1
      this.orderList()
    },
    async orderList(){
        let res = await orders({
            type:this.orderType,
            include:"items.product,shop",
            page:this.pages
        })
        this.total_pages = res.meta.pagination.total_pages;
        this.Order =this.Order.concat(res.data)  
        if(res.meta.pagination.total == 0)  {this.type1 = "A"}
        else { this.type1 = "b"}
          this.Order.map(item=>{
            item.ordkey = 1
            if(item.order_status == "closed"){item.ordkey ="交易关闭"}  //交易关闭
            else if(item.order_status == "wait_pay"){item.ordkey ="待付款"}  //取消订单 付款
            else if(item.order_status == "wait_delivery"){item.ordkey ="待发货 "} // 崔发货
            else if(item.order_status == "wait_received"){item.ordkey ="待收货"} // 确认收货 
            else if(item.order_status == "received"){item.ordkey ="已收货"} // 申请退款 
            else if(item.order_status == "completed") {item.ordkey ="交易完成"} //申请退款 申请换货
        })
    },

    async cancel(item,index){//取消订单
        let res = await ordersPatch({ 
            ID:item.id,
        }) 
        console.log(res,"取消订单")
        if(res){
            res.ordkey ="交易关闭"
            this.Order = [...this.Order]
            this.Order[index] = res
        }
    },
   async delecl(item,index){//删除
         let res = await ordersDelete({
             ID:item.id,
         })
         if(res !=""){
            console.log(res,"删除订单")
            this.Order.splice(index,1)
         }
    },
    async Sure(item,index){//确认收获
         let res = await ordersPut({
            ID:item.id,
         })
         if(res !=""){
            console.log(res,"确认收获")
            this.Order.splice(index,1)
         }

    },
    OrDeta(id,item){
         console.log(item)
       
        //this.$router.push({path: "/OrderDeta",query:{id:id}});
    },
    goright(){
        this.$router.push({ path: "/Address" });
    },
    payment(item){
           console.log(item)
           let payment ={
                id:item.id,
                price:item.total_amount,
                goback:1,
            }
           this.$router.push({path:"/Payment",query:payment});
    },
    Rate(item){//评论
     this.$storage.setSession("Rate", item);
       this.$router.push({ path: "/Rate"});
    },
    refund(item){//申请退款
        this.$router.push({path:"/Refund",query:{id:item.id}});
    },
    logistics(item){//填写物流
         this.$router.push({path:"/Logistics",query:{id:item.id,key:1}});
    }
  }
};
