import {categoryWithSpecById , Category } from "@/api/user";
export default { 
  name: "Screen",
    components:{
    },
    props: {
        query:{
         type:Object
        }
       
    },
    watch: {
      query(n,o){
       
      },
     deep:true
    },
  data() {    
    return{ 
        isactive:0,
        query1:this.query,
        Pri_list:["神佛","瑞兽","仿古","山水","人物","生肖","动物","花"],
        priceList:[
            {min:"0",max:"1000",isActive:false},
            {min:"1000",max:"3000",isActive:false},
            {min:"3000",max:"10000",isActive:false},
            {min:"10000",max:"50000",isActive:false},
            {min:"50000",max:99999999999,isActive:false}
        ],
        children:[],//种类
        specs:[],//属性
        Arr:[],
        ProductList:[],
        minPrice:"",//最低价
        maxPrice:"",//最高价
        minWeight:"",//最低重量
        maxWeight:"",//最高重量
        minDiameter:"",//最低圈口
        maxDiameter:"",//最高圈口
        catid:this.query.Choice.catid,
        Inform:[],
    };
  },
    created(){
        if(this.query.page == 9 ){
            this.Category()
        }
        this.Categorys()
    },
   
  methods: {
    async Category() {
      //请求数据接口
      let res = await Category({});
      let headLiet =[]
      res.data.map(item =>{
          item.isactive = false
         if(item.level == 1) headLiet.push(item)  
      })
      this.Inform = headLiet
      this.Inform[0].isactive = true
      this.catid = this.Inform[0].id
    },
    async Categorys() {//请求数据接口 
        let res = await categoryWithSpecById({
            ID:this.catid,
        });
       let page = this.query.page
       this.children = res.children
       this.children.map(item => {item.isActive = false}) 
       this.specs = res.specs
       this.specs.map(item => {
            item.spec_values.map(page=>{
                page.isActive = false
            })
        }) 
      if(page == 1) { //已经选择了种类
           let specs = this.query.Choice.specs
           let specs1 = this.query.Choice.specs1
         for(let i = 0;i < this.specs.length;i++){
              if(specs == this.specs[i].id){
                for(let j = 0;j < this.specs[i].spec_values.length;j++){
                    if(specs1 == this.specs[i].spec_values[j].id){
                        this.specs[i].spec_values[j].isActive = true
                    }
                }
              }
          }
      } 
    },
    Serform(id,item,index){
        this.Inform = [...this.Inform]
         this.Inform.map(key =>{
              key.isactive = false
         })
         item.isactive = true
         if(id ==  this.catid) return
         else     this.catid = id
         this.Categorys()
       
 
    },
    childClick(item,index){ //点击分类
        this.children = [...this.children]
        this.children[index].isActive =! this.children[index].isActive
    },
    specsClick(name,index,item,dex){//点击属性
        this.specs = [...this.specs]
       this.specs[dex].spec_values[index].isActive =! this.specs[dex].spec_values[index].isActive
      
    },
    money(item,index){
        for(var i = 0;i<this.priceList.length;i++){
            if(index == i){
                this.priceList[i].isActive =!  this.priceList[i].isActive
                if(this.priceList[i].isActive == true){
                        this.minPrice = item.min
                        if(item.max == 99999999999){
                            this.maxPrice = ""
                        }else{
                            this.maxPrice = item.max
                        }
                }else{
                    this.minPrice = "" 
                    this.maxPrice = ""
                }
            }else{
                this.priceList[i].isActive = false
            }
        }
    },
    pricemin(e){
        this.minPrice = e.target.value.replace(/[^\d]/g,"");
        this.priceList.map(item=>{
            item.isActive =false
        })
    },
    pricemax(e){
        this.maxPrice = e.target.value.replace(/[^\d]/g,"");
        this.priceList.map(item=>{
            item.isActive =false
        })
    },
    clear(){ //清楚筛选
        this.minPrice = this.maxPrice = this.minWeight = this.maxWeight = this.minDiameter =this.maxDiameter = ""
        this.priceList.map(item=>{
               item.isActive = false
        })
        this.children.map(item => {item.isActive = false}) 
        this.specs.map(item => {
            item.spec_values.map(page=>{
                page.isActive = false
            })
        }) 

    },
    complete(){ //点击完成
        let aaa = [];
        this.specs.map(item=>{
            aaa[item.id] = []
            let itemsss = item.spec_values.filter(x=> {
                return x.isActive
            })
            itemsss.map(i=>{
                aaa[item.id].push(i.id)
            })
        })
        let bbb =[]
        for(var i=0;i<aaa.length;i++){
            if(aaa[i] !== undefined && aaa[i] !=="" && aaa[i] !== null){
               bbb.push(aaa[i])
            }
        }
        let Edid = {
            specs:bbb,
            category_id:this.query.Choice.catid,
            min_price:this.minPrice,
            max_price:this.maxPrice,
            min_weight:this.minWeight,
            max_weight:this.maxWeight,
            min_inner_diameter:this.minDiameter,
            max_inner_diameter:this.maxDiameter,
            recommend:"",
            order_price:"",
            order_time:"",
            catid:this.catid
        }
        this.$emit('showEdit',Edid)
    }     
  }
};
