import XHeader from '../common/header.vue'
import { Search , List} from 'vant';
import { GetAtration , DAration , PutAration , UpAration ,YsAration, NoAration } from "@/api/shop";
export default {
  name: "Administration",
  components:{
      XHeader, 
      Search,
      List
  },
  data() {    
    return{ 
       value:"",
       active:0,
        box1:[{name:"全部",status:"",sale_status:""},
             {name:"售卖中",status:"shelve",sale_status:"on-sale"},// 售卖中 status=shelve&sale_status=on-sale
             {name:"已售卖",status:"shelve",sale_status:"sold"}, // 已售卖 status=shelve&sale_status=sold
             {name:"已下架",status:"un-shelve",sale_status:""},// 已下架 status=un-shelve
             {name:"未通过",status:"fail",sale_status:""},//        未通过 status=fail
           ],
        fide1:false,
        status:"",
        sale_status:"",
        loading: false,
        finished: false,
        pages: 1,
        total_pages: 1,
        OList:[]
    };
  },
 created() {
    this.Adlist()
 },
methods: {
    goBack(){
        this.$router.go(-1);
    },
    goright(){
       this.$router.push({ path: "/jadeType"});
    },
    onSearch(){ },
    onLoad() {
      // 异步更新数据
      setTimeout(() =>{
        if (this.pages < this.total_pages) {
          this.pages++;
          // 加载状态结束
          this.Adlist();
        }
        this.loading = false;
        // 数据全部加载完成
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
    Edit(item){
        this.$storage.setSession("AdEdit", item);
        this.$router.push({ path: "/release",query:{ID:item.category_id,pae:1}});

    },
    boxk(item,index){
        this.OList =[]
        this.pages = 1
        this.active = index
        this.status = item.status
        this.sale_status = item.sale_status
        this.Adlist()
    },
   async Adlist(type){
         let res = await GetAtration({
              status: this.status ,
              sale_status:this.sale_status,
         })
        this.OList =this.OList.concat(res.data)  
        this.total_pages = res.meta.pagination.total_pages;
         console.log(res,"@@@@@@@@@@@@@@@@@@@@@@@@@")
    },
    
   async dele(item,index){ //删除
         let res = await DAration({
             ID:item.id
         })
         console.log(res)
         if(res == "" || res == null){
              this.OList.splice(index,1)
         }
    },
     async Lower(item,index){ // 下架
         let res = await PutAration({
             ID:item.id
         })
         console.log(res)
         if(res){
             this.OList = [...this.OList]
             if(this.active == 0){
               this.OList[index] = res
             }else{
               this.OList.splice(index,1)
             }
              
         }
    },
    async Updata(item,index){//上架
        let res = await UpAration({
            ID:item.id
        })
        console.log(res)
        if(res){
             this.OList = [...this.OList]
             if(this.active == 0){
               this.OList[index] = res
             }else{
               this.OList.splice(index,1)
             }
        }
    },
    async Sell(item,index){//标记为已售
        let res = await NoAration({
            ID:item.id
        })
        console.log(res)
        if(res){
             this.OList = [...this.OList]
             if(this.active == 0){
               this.OList[index] = res
             }else{
               this.OList.splice(index,1)
             }
        }
    },
    async NoSell(item,index){//标记为可售
        let res = await YsAration({
            ID:item.id
        })
        console.log(res)
        if(res){
             this.OList = [...this.OList]
             if(this.active == 0){
               this.OList[index] = res
             }else{
               this.OList.splice(index,1)
             }
        }
    },
    //
    Details(item){
        let commodity = {};
        commodity.Pid = item.id;
        this.$storage.setSession("commodity",commodity);
        this.$router.push({ path: "/Details" });
    }

},
  
    
   
   
  
};
