import { WithSpec } from "@/api/user";
export default {
  name: "Datatype",
  components: {

  },
  data(){
    return{
       active:0,
       Navlist: [
           {category_name:"推荐",id:"0",child:[],specs:[]}
       ],
       listType:null,//分类
       listPage:null,//分类属性
       specs:[]
    };
  },
  created(){ 
        this.WithSpecs()
  },
  methods: {
    async WithSpecs() {//请求数据接口 
        let route = this.$route.query;
        let res = await WithSpec({}); 
        this.Navlist = this.Navlist.concat(res.data)  //所有
        console.log( this.Navlist ,"@@@@@@@@@@@@@@@@@@@@@")
        console.log(res.data)
        res.data.map(item=>{
            item.children.leng = 8
            item.specs.map(type=>{
                 type.leng = 8
            })
        })
        
        for(var i = 0;i<this.Navlist.length;i++){
           if(route.key.product_name == this.Navlist[i].category_name){
                this.active = i 
           }
        }
        if(this.active == 0)this.listType = this.Navlist[1].children  //分类      
        else this.listType = this.Navlist[this.active].children  //分类
        this.listPage = this.Navlist[this.active].specs  //分类属性
        
        
        
    },
    goBack(){
         this.$router.go(-1);
    },
    Datatype(item,index){
        this.active = index; 
        if (index == 0) this.listType = this.Navlist[1].children  //分类 
        else  this.listType = this.Navlist[this.active].children  //分类
       
         this.listPage = this.Navlist[this.active].specs  //分类属性
    },
    DetaChoice(item,key,size){ //点击分类属性列表页 item 一级   
            let parameter = {}
            let Choice = {}
            if(size === 0 ){// 不带属性 page === 0
                Choice.catid  =  item.id  //筛选|| 查询商品自身ID
                item.product_name = item.category_name
                parameter.page = 0
                parameter.type = item
                parameter.Choice = Choice
                
            }else{ // 带属性 page === 1
                Choice.catid  =  this.Navlist[this.active].id  //筛选||查询商品 总ID  ID  
                Choice.specs  = item.id
                Choice.specs1 = key.id
                // specs [总属性id[当前属性ID]] specValueId
                item.product_name = key.spec_value
                parameter.page = 1;
                parameter.type = item
                parameter.item = key  
            }
            parameter.Choice =Choice
            this.$storage.setSession('parameter', parameter)     
            this.$router.push({
                path: "/DetaChoice",
            });
    },
    Open(key,dex){//展开
        if(key == 'a'){
            this.listType =[...this.listType]
            this.listType.leng =this.listType.length; 
        }else{
             this.listPage =[...this.listPage]
             this.listPage[dex].leng =100
        }
    },
    Take(key,dex){
        if(key == 'a'){
           this.listType =[...this.listType]
           this.listType.leng = 8 
        }else{
            this.listPage =[...this.listPage]
            this.listPage[dex].leng =8
        }
        
    },

  }
};
