import { Rate, Swipe, SwipeItem ,Toast , Popup ,List  } from "vant";
import { products, carts , queryList , cartList ,favorite } from "@/api/user";
export default {
  name: "Details",
  inject:['reload'],
  components: {
    Rate, Swipe, SwipeItem ,Toast , Popup ,List
  },
  data() { 
    return {
      vehiclet:0,
      popupShow:false,
      value: 5,
      current: 0,
      currentLength: "",
      disabled: true,
      Iheader: false,
      Iheader1: true,
      commodity: this.$storage.getSession("commodity"),
      DetaList: null,
      has_favorite:false,
      shop: {},
      category_id:"",
      Alllist:[],
      loading: false,
      finished: false,
      pages:1,
      total_pages:1,
    };
  },
  created() {
    Toast.loading({ mask: true,message: '加载中...'});
    this.add();
    this.comm();
    this.cart()
  },
  methods: {
     onLoad() {
      setTimeout(() => {
        if (this.pages < this.total_pages) {
          this.pages++;
          this.comete();
        }
        this.loading = false;
        if (this.pages >= this.total_pages) {
          this.finished = true;
        }
      }, 2000);
    },
     async cart() {
      //加入购物车
        let res = await cartList({});
        let len = 0
        res.map(item=>{
           item.map(key=>{
              len ++ 
           })
        })
        this.vehiclet = len      
    },
    async comm() {
        let res = await products({
          ID:this.commodity.Pid 
        });
      console.log(res)
      res.shop.avatar = res.shop.avatar.replace(/shop.jigesi.test/,'192.168.0.160')
      this.DetaList = res;
      this.currentLength = res.images.length;
      this.shop = res.shop;
      this.category_id = res.category_id
      console.log(res,"@@@@@@@@@@@@@@@@@@@@@@@@@@@@@",res.has_favorite)
      if(res.has_favorite != undefined){
         this.has_favorite = true
      }
      this.comete()
    },
      async comete() {//请求数据接口 
        let bbb =[]
        let res = await queryList({
            specs:JSON.stringify(bbb),
            recommend:"",
            order_price:"", 
            order_time:"",
            category_id:this.category_id,
            min_price:"", 
            max_price:"",
            min_weight:"",
            max_weight:"",  
            min_inner_diameter:"", 
            max_inner_diameter:"",
            page:this.pages
        });
        for(var i = 0; i< res.data.length ; i ++){
             for(var j=0;j<res.data[i].images.length ; j++){
                 res.data[i].images[j] = res.data[i].images[j].replace(/shop.jigesi.test/,'192.168.0.160')
             }
        }
        this.Alllist = this.Alllist.concat(res.data) 
        if( this.Alllist == ''){ this.contEmpty = false }
        this.total_pages = res.meta.pagination.total_pages
    },
    goBack() {
      this.$router.go(-1);
    },
    add() {
      var _this = this;
      window.addEventListener("scroll", function() {
        var scrollTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop > 100) {
          _this.Iheader = true;
          _this.Iheader1 = false;
        } else if (scrollTop < 100) {
          _this.Iheader = false;
          _this.Iheader1 = true;
        }
      });
    },
    onChange(index) {
      this.current = index;
    },
    Sure() {
      //立即购买
      let item = this.DetaList;
      item.orderType = 0;
      if(this.DetaList.sale_status == 'sold'){
          Toast.fail('已经售出');
          return
      }
      this.$storage.setSession("SureItem", item);
      this.$router.push({ path: "/Sure" });
    },
    async Addcart() {  //加入购物车
      if(this.DetaList.sale_status == 'sold'){
          Toast.fail('已被下单');
          return
      }
      let res = await carts({
        product_id: this.DetaList.id
      }).then(res=>{Toast('加入成功');this.vehiclet++})
        .catch(err=>{Toast('网络异常，请稍后再试');})
    },
    shopCart(){//进入购物车
      this.$router.push({ path: "/cartList" });
    },
    information() { //店铺信息
      console.log(this.DetaList);
      this.$storage.setSession("shopInform", this.DetaList.shop.id);
      this.$router.push({  path: "/shopInform", });
    },
    async Collection(){//收藏
         let res = await favorite({
           ID:this.DetaList.id
         }).then(res=>{
                if(this.has_favorite){
                    Toast('取消成功');
                    this.has_favorite = false;
                }else{
                  Toast('收藏成功');
                    this.has_favorite = true;
                }
          })
          .catch(err=>{Toast('网络异常，请稍后再试');})
        
    },
    Private(){//私信
        Toast.fail('暂未开通');
    },
    Popup(){
      this.popupShow = true
    },
      //点击进入商品详情
    Details(id) {
        let commodity = {};
        commodity.Pid = id;
        this.$storage.setSession("commodity", commodity);
        this.reload();
        Toast.loading({ mask: true,message: '加载中...'});
    },
    Measures(){ //交易保障措施
      this.$router.push({  path: "/Measures", });
    },
  }
};
