type:Object
}
},
watch: {
Objress(n,o){
this.item = n
console.log(n)
if(n == undefined ){
this.Kname =""
this.Telephone =""
this.address =""
this.name = "添加收货地址"
}else if(n ==  null){
this.Kname =""
this.Telephone =""
this.address =""
this.name = "添加收货地址"
}else{
this.name = "修改收货地址"
this.lang = n
console.log(n,"1111111")
this.Kname = n.contract_name
this.Telephone = n.contract_phone
this.ol =n.province + n.city +n.district
this.ola = n.province
this.olb = n.city
this.olc = n.district
this.address = n.address
this.Subm = 1
this.ID = n.id
this.dex = n.index
}
},
deep:true
},
created() {
},
methods: {
goBack(){
this.$emit('showEdit')
},

puttext(){
let Kname =this.Kname
let Telephone =this.Telephone
let address =this.address 
if(Kname.length > 1 && Telephone.length >10 && this.value1 !="" && this.address !=""){

this.butt = false
}
},
